import React, { useState, useEffect } from "react";
import { MDBContainer } from "mdbreact";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faLaptopCode,
  faObjectUngroup
} from "@fortawesome/free-solid-svg-icons";
import "./portfolio.scss";
import ProjectTemp from "./devProject";
import { projectDetails, projectDetailsTwo } from "./projectDetails";
import uuid from "uuid";
import { CSSTransition } from "react-transition-group";
import ProjectClear from "./Projectdescriptions";

export default function Portfolio() {
  const [gategory, setGategory] = useState(false);
  const [select, setSelect] = useState();
  const [devproject, setDevProject] = useState();
  const [projectEnter,setProjectenter] = useState(false)

  useEffect(() => {
    if (select) {
      let project = projectDetails.filter(({ name }) => name === select);
      setDevProject(project);
    }
  }, [select]);


  return (
    <MDBContainer className="portoflio">
      <h1>Portfolio</h1>
      <div className="CategoryCont">
        <span
          onClick={() => {
            setGategory(false);
            setProjectenter(false)
            setSelect();
          }}
          style={{ background: !gategory ? "#78909C" : null }}
        >
          <p>
            <FontAwesomeIcon icon={faLaptopCode} />
          </p>
        </span>
        <span
          onClick={() => {
            setGategory(true);
            setProjectenter(false)
            setSelect();
          }}
          style={{ background: gategory ? "#78909C" : null }}
        >
          <p>
            <FontAwesomeIcon icon={faObjectUngroup} />
          </p>
        </span>
      </div>
      <CSSTransition in={gategory} timeout={200} classNames="page">
        {!select ? (
          <div className="projectCont">
            {!gategory
              ? projectDetails.map(({ image, name }) => (
                  <ProjectTemp
                    key={uuid()}
                    image={image}
                    name={name}
                    type={"Web Development"}
                    select={() => {
                      setSelect(name);
                      setProjectenter(true)
                    }}
                  />
                ))
              : projectDetailsTwo.map(({ name, image }) => (
                  <ProjectTemp
                    key={uuid()}
                    image={image}
                    name={name}
                    type={"Web Desing"}
                  />
                ))}
          </div>
        ) : (
          <CSSTransition in={projectEnter} timeout={500} classNames="node">
          <React.Fragment>
            {devproject ? (
              devproject.map(
                ({
                  image,
                  name,
                  description,
                  techStacks,
                  year,
                  github,
                  link
                }) => (
                  <ProjectClear
                  key={uuid()}
                    image={image}
                    name={name}
                    description={description}
                    techStacks={techStacks}
                    year={year}
                    github={github}
                    link={link}
                    isVisible={select}
                    cancel={()=>{
                      setSelect()
                      setProjectenter(false)
                    }}
                  />
                )
              )
            ) : (
              <div className="spinner-border" role="status">
                <span className="sr-only">Loading...</span>
              </div>
            )}
          </React.Fragment>
          </CSSTransition>
        )}
      </CSSTransition>
    </MDBContainer>
  );
}
