import React,{useState} from "react";
import { MDBInput, MDBBtn, MDBIcon } from "mdbreact";


export default function ContactForm() {


  const [name,HandelName] = useState("")
  const [email,HandelEmail] = useState("")
  const [subject,HandelSubject] = useState("")
  const [message,HandelMessage] = useState("")

 const [value,setValue] = useState({})

const HandelSubmit = (e) => {
  e.preventDefault() 

  setValue((value)=>({
    ...value,
    name,
    email,
    subject,
    message
  }))
}
 console.log(value)
  return (
    <form className="forms" onSubmit={HandelSubmit}>
      <p className="h5 mb-4">fell free to contact me</p>
      <div className="grey-text">
        <MDBInput
          label="Your name"
          onChange={(e)=>{
            HandelName({[e.target.name]: e.target.value})
          }}
          icon="user"
          group
          type="text"
          validate
          error="wrong"
          success="right"
        />
        <MDBInput
          label="Your email"
          onChange={(e)=>{
            HandelEmail({[e.target.name]: e.target.value})
          }}
          icon="envelope"
          group
          type="email"
          validate
          error="wrong"
          success="right"
        />
        <MDBInput
          label="Subject"
          onChange={(e)=>{
            HandelSubject({[e.target.name]: e.target.value})
          }}
          icon="tag"
          group
          type="text"
          validate
          error="wrong"
          success="right"
        />
        <MDBInput
          type="textarea"
          onChange={(e)=>{
            HandelMessage({[e.target.name]: e.target.value})
          }}
          rows="2"
          label="Your message"
          icon="pencil-alt"
        />
      </div>
      <div className="text-center">
        <MDBBtn outline color="secondary" type='submit'>
          Send <MDBIcon far icon="paper-plane" className="ml-1" />
        </MDBBtn>
      </div>
    </form>
  );
}
